import { Component, Inject, OnDestroy, OnInit, Renderer2, ViewEncapsulation } from '@angular/core';
import { VariablesService } from '../_helpers/services/variables.service';
import { DOCUMENT } from '@angular/common';
import { BackendService } from '../_helpers/services/backend.service';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, interval } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-exchange',
  templateUrl: './exchange.component.html',
  styleUrls: ['./exchange.component.scss']
})
export class ExchangeComponent implements OnInit, OnDestroy {
  title = 'widget';
  textScript = null;
  public priceNonkycUsdt = 'No Data';
  public priceNonkycBtc = 'No Data';
  public priceXeggexUsdt = 'No Data';
  public priceXeggexBtc = 'No Data';
  public priceOgreUsdt = 'No Data';
  public priceFinancexUsdt = 'No Data';
  public priceFinancexXmr = 'No Data';


  constructor(
    private httpClient: HttpClient,
    public variablesService: VariablesService,
    private backend: BackendService,
    @Inject(DOCUMENT) private document: Document,
    private renderer2: Renderer2
  ) {
  }

  handleError(error: HttpErrorResponse) {
    let errorMessage = 'Unknown error!';
    if (error.error instanceof ErrorEvent) {
      // Client-side errors
      errorMessage = `Error: ${error.error.message}`;
    } else {
      // Server-side errors
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(errorMessage);
  }

  getPriceData(url: string) {
    return this.httpClient.get(url).pipe(
      catchError(this.handleError),
      map((res: any) => {
        return res;
      })
    );
  }

  getDataPrice() {
    this.getPriceData('https://tradeogre.com/api/v1/ticker/NIR-USDT').subscribe((response: any) => {this.priceOgreUsdt = Number(response.price).toFixed(4).toString();});
    this.getPriceData('https://api.xeggex.com/api/v2/asset/getbyticker/NIR').subscribe((response: any) => {this.priceXeggexUsdt = Number(response.usdValue).toFixed(4).toString();});
    this.getPriceData('https://api.nonkyc.io/api/v2/asset/getbyticker/NIR').subscribe((response: any) => {this.priceNonkycUsdt = Number(response.usdValue).toFixed(4).toString();});
    this.getPriceData('https://financex.trade/api/v2/trade/public/currencies/nir').subscribe((response: any) => {this.priceFinancexUsdt = Number(response.price).toFixed(4).toString();}); 
  }

  openInBrowser(url: string) {
    this.backend.openUrlInBrowser(url);
  }
  
  ngOnInit(): void {
    this.textScript = this.renderer2.createElement('script');
    this.textScript.src = 'assets/scripts/lcw-widget.js';
    this.renderer2.appendChild(this.document.body, this.textScript);
    this.getDataPrice();
    interval(60000).subscribe(() => {
      this.getDataPrice();
    });
  }
  ngOnDestroy(): void {
    this.renderer2.removeChild(this.document.body, this.textScript);
  }
}
