import { Component, OnInit, NgZone } from '@angular/core';
import { BackendService } from '../_helpers/services/backend.service';
import { DeeplinkParams, PushOffer, CancelOffer, Wallet } from './../_helpers/models/wallet.model';
import { MIXIN } from '../_shared/constants';
import { BigNumber } from 'bignumber.js';
import { VariablesService } from '../_helpers/services/variables.service';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-swap',
  templateUrl: './swap.component.html',
  styleUrls: ['./swap.component.scss']
})
export class SwapComponent implements OnInit {
  offerForm: FormGroup;
  secondStep = false;
  actionData: DeeplinkParams = {}
  walletToPayId = 0;
  marketplace_offer_send = false;
  marketplace_offer_create = false;
  marketplace_offer_cancel = false;
  nextStepInterval;
  marketplaceModalShow = true;
  copyAnimation = false;
  marketplaceConfirmHash: any = null
  marketplaceError: any = null
  sendRoute = false;
  defaultMixin = MIXIN;
  data:any = {};
  offers:any = [];
  total_offers = 0;
  

  constructor(private backend: BackendService, private ngZone: NgZone, public variablesService: VariablesService, private _router: Router, private fb: FormBuilder, private http: HttpClient) { }

  ngOnInit() {
    this.getOffers();
    this.offerForm = this.fb.group({
      title: [''],
      category: ['', Validators.required],
      description: ['', Validators.required],
      price: ['', Validators.required],
      contact: ['', Validators.required],
      tx_id: [''],
      comment: ['']
    });
  }

  async getOffers() {
    try {
      const res = await this.http.post("http://127.0.0.1:11113/json_rpc", {
        jsonrpc: "2.0",
        id: "0",
        method: "marketplace_global_get_offers_ex",
        params: {
          filter: {
            amount_low_limit: 0,
            amount_up_limit: 0,
            bonus: false,
            category: "",
            fake: false,
            keyword: "",
            limit: 100,
            location_city: "",
            location_country: "",
            offer_type_mask: 0,
            offset: 0,
            order_by: 0,
            primary: "",
            rate_low_limit: "0.000000",
            rate_up_limit: "0.000000",
            reverse: false,
            target: "",
            timestamp_start: 0,
            timestamp_stop: 0,
          },
        },
      }).toPromise();
      this.data = res
      this.offers = this.data.result.offers;
      this.total_offers = this.data.result.total_offers;
    } catch (err) {
      console.error("fetch error", err);
    }
    console.log("DATA--------------------------->",this.offers, this.total_offers)
  }

  submitOffer(): void {
    this.marketplace_offer_create = false;
    if (this.offerForm.valid) {
      const formData = this.offerForm.value;
      this.actionData.price = formData.price;
      this.actionData.title = formData.title;
      this.actionData.category = formData.category;
      this.actionData.description = formData.description;
      this.actionData.contact = formData.contact;
      this.actionData.comment = formData.comment;
    }
  }

  marketplaceSend(): void {
    let offerObject: PushOffer = {
      wallet_id: this.walletToPayId,
      od: {
        ap: this.actionData.price || '100',
        at: '1',
        cat: this.actionData.category || '',
        cnt: this.actionData.contact || '',
        com: this.actionData.comment || this.actionData.comments || '100',
        do: this.actionData.description || '',
        et: 10,
        fee: new BigNumber(
          '' +
            (+this.actionData.fee || +this.variablesService.default_fee) *
              1000000000000
        ),
        lci: '',
        lco: 'World Wide',
        ot: 1,
        pt: 'Credit cards, BTC, NiR, ETH',
        t: this.actionData.title || '',
        url: this.actionData.url || this.actionData.img_url || '',
      },
    };
    this.backend.push_offer(offerObject, (status, data) => {
      this.ngZone.run(() => {
        if (data.success) {
          this.marketplaceModalShow = false;
          this.marketplaceConfirmHash = data.tx_hash;
        } else {
          this.canselAction();
        }
      });
    });
    this.marketplace_offer_send = false;
  }

  marketplaceCancelSend() {
    this.marketplace_offer_cancel = false;
    if (this.offerForm.valid) {
      const formData = this.offerForm.value;
      this.actionData.tx_id = formData.tx_id;
    }
    this.cancelOffer();
  }

  cancelOffer(): void { 
    let offerObject: CancelOffer = {
        //wallet_id: this.walletToPayId,
        tx_id: '587b4099b38fe830637bc153c88f3a2038e5780bef87da1d2b4122ef93c75d0d',
        no: 0
    };
    this.backend.cancel_offer(offerObject, (status, data) => {
        if (data.success) {
          this.marketplaceModalShow = false;
          this.marketplaceConfirmHash = data.tx_hash;
        } else {
          this.marketplaceModalShow = false;
          this.canselAction();
        }
    });
  }

  copyHash(): void {
    this.backend.setClipboard(this.marketplaceConfirmHash);
    this.copyAnimation = true;
    setTimeout(() => (this.copyAnimation = false), 2000);
  }

  canselAction(): void {
    this.variablesService.deeplink$.next(null)
    this.variablesService.sendActionData$.next({});
    this.actionData = {};
    this.secondStep = false;
    this.marketplace_offer_send = false;
  }

}
